<template>
  <div>
      <app-header></app-header>
      <transition name="fade" mode="out-in">
          <router-view></router-view>

      </transition>
      <!-- <app-product-list></app-product-list> -->
      <!-- <app-product-sell></app-product-sell> -->
      <!-- <app-product-purchase></app-product-purchase> -->
      <app-footer></app-footer>
  </div>

</template>
<script>
import Footer from "./components/shared/footer"
import Header from "./components/shared/header"
import ProductPurchase from "./components/products/productPurchase"
import ProductSell from "./components/products/productSell"
import ProductList from "./components/products/productList"


export default {
    components : {
        appHeader : Header,
        appProductPurchase : ProductPurchase,
        appFooter : Footer,
        appProductSell : ProductSell,
        appProductList : ProductList
    },
    created(){
        this.$store.dispatch('getTradeResults')
        this.$store.dispatch('initApp')
    }
} 
</script>

<style>
body{
    background-color : #a7a0bb5c;
}

.fade-enter {
    opacity: 0;
}
.fade-enter-active {
    transition: opacity .3s ease-out; 
}
.fade-leave {}
.fade-leave-active {
    transition: opacity .3s ease-out; 
    opacity: 0;
}
</style>



