<template>
  
<div class="container">
<div class="loading" :style="isLoading">
    <div class="lds-ripple">
        <div></div>
        <div></div>
    </div>
</div>
    <div class="row">
        <div class="col-6 offset-3 pt-3 card mt-5 shadow">
            <div class="card-body">
                <h3>rn lemleri</h3>
                <hr>
                <div class="form-group">
                    <label>rn Ad</label>
                    <input type="text" class="form-control" placeholder="rn adn giriniz.." v-model="product.title">
                </div>
                <div class="form-group">
                    <label>Adet</label>
                    <input type="number" class="form-control" placeholder="rn adetini giriniz.." v-model="product.count">
                </div>
                <div class="form-group">
                    <label>Fiyat</label>
                    <input type="number" class="form-control" placeholder="rn fiyat giriniz.." v-model="product.price">
                </div>
                <div class="form-group">
                    <label>Aklama</label>
                    <textarea cols="30" rows="5" placeholder="rne ait bir aklama giriniz..."
                              class="form-control" v-model="product.description"></textarea>
                </div>
                <hr>
                <button class="btn btn-primary" :disabled="saveEnabled" @click="saveProduct">Kaydet</button>
            </div>
        </div>
    </div>
</div>
</template>
<script>


export default {
    data(){
        return {
            product : {
                title : '',
                count : 0,
                price : 0,
                description : '',
            },
            saveButtonClicked : false,

}
    },
    methods : {
        saveProduct(){
            this.saveButtonClicked = true
            this.$store.dispatch('saveProduct',this.product)
        }
    },
    computed : {
        saveEnabled(){
            if(this.product.title.length > 0 && this.product.count > 0 && this.product.price > 0 && this.product.description.length > 0){
                return false;
            }else{
                return true;
            }
        },
        isLoading(){
            if(this.saveButtonClicked){
                return {
                    display : "block"
                }
            }else{
                return {
                    display : "none"
                }
            }
        }
    },
    beforeRouteLeave(to,from,next){
        if((this.product.title.length > 0 || this.product.count > 0 || this.product.price > 0 || this.product.description.length > 0) && this.saveButtonClicked == false){
            if(confirm("There are unsaved changes. You want to leave?")){
                next();
            }else{
                next(false);
            }
        }else{
            next()
        }

    } 

} 
</script>

<style scoped>

</style>



