import axios from "axios";
import { router } from "../../router"

const state = {
    products : [],
}

const getters = {
    getProducts(state) {
        return state.products;
    },
    getProduct(state) {
        return key => state.products.filter(product => {
            return product.key == key
        } )
        
    }
}   

const mutations = {
    updateProductList(state,product) {
        state.products.push(product);
    }
}

const actions = {
    initApp({commit}) {
        //axios islemleri
        axios.get("https://product-operations-bc8f8-default-rtdb.firebaseio.com/products.json")
        .then(response => {
            let data = response.data
            for(let key in data) {
                data[key].key = key;
                commit("updateProductList",data[key]);
            }
        })
    },
    saveProduct({dispatch,commit,state},product){
        axios.post("https://product-operations-bc8f8-default-rtdb.firebaseio.com/products.json",product)
        .then(response => {
            product.id = response.data.name

            commit("updateProductList",product)
            console.log(state.products)

            let tradeResult = {
                purchase : product.price,
                sale : 0,
                count : product.count

            }
            dispatch('setTradeResults',tradeResult)
            router.replace("/")
        })
    },
    sellProduct({state,dispatch, commit},product){ 
        let produc = state.products.filter(p => {
            return p.key == product.key
        } )

        if(produc){
            let totalCount = produc[0].count - product.count

            axios.patch("https://product-operations-bc8f8-default-rtdb.firebaseio.com/products/" + product.key + ".json",{ count : totalCount })
            .then(response => {
                produc[0].count = response.data.count 
                let tradeResult = {
                    purchase : 0,
                    sale : produc[0].price,
                    count : product.count
    
                }
                dispatch('setTradeResults',tradeResult)
                router.replace("/")
            })
        }

    }
}

export default {
    state,getters,mutations,actions
}


