import axios from "axios"

export const setTradeResults = ({state,commit},tradeResult)=> {
    commit('updateTradeResult',tradeResult)

    let tradeData = {
        purchase : state.purchase,
        sale : state.sale
    }

    axios.put('https://product-operations-bc8f8-default-rtdb.firebaseio.com/trade-result.json',tradeData)
    .then(response => {
        console.log(response)
    })
}

export const getTradeResults = ({commit})=>{
    axios.get('https://product-operations-bc8f8-default-rtdb.firebaseio.com/trade-result.json')
    .then(response =>{
        console.log(response)
        commit('updateTradeResult',response.data)
    })
}


